@import "./variables.module.scss";
main{
//MainCar Img
height: 850px;
.MainCarImg__section{
    img{
        width: 100%;height: 200px;
        margin:0 auto;
        margin-top:20px;
    }   
}

//MainCarRegistranion
.MainCarRegistration__section{
    width: 90%; height: 250px;
    margin:0 auto;
    border-radius: 10%;
    background-color: $background-color;
    .MainCarRegistration__box{
        padding-top:4px;
        .MainCarRegistration__choice{
            margin:20px 20px;
            text-align: center;
            p{
                margin-bottom:5px;
                font-size:19px;
            }
            select{
                width: 170px;
                height: 30px;
                text-align: center;
            }
        }
    }
}

//MacinCarConnector
.MainCarConnector__section{
    width: 90%; height: 250px;
    margin:0 auto;
    margin-top:20px;
    border-radius: 10%;
    background-color: $background-color;
    height:280px;
    .MainCarConnector__box{
        .MainCarConnector__choice{
            text-align: center;
            padding-top:1px;
            p{
                margin-top:20px;
            }
            .MainCarConnector__menu{
                margin:0 auto;
                margin-top:10px;
                width: 80%;
                display:grid;
                grid-template-columns: repeat(3, 1fr);
                div{
                    background-color: $main-color;
                    color:white;
                    width: 83%;
                    height: 40px;
                    border-radius: 10px;
                    margin:10px 20px;
                    line-height: 30px;
                    font-size:13px;
                }
            }
        }
    }
    .MainCarNumber__box{
        margin-top:20px;
        .MainCarNumber__search{
            text-align: center;
            p{
                margin-bottom:10px;
            }
            input{
                width: 60%;
                height: 37px;
                text-align: center;
            }
       
            
        }
    }
}
}
