@import "./variables.module.scss";


// search
.searchSection{
    .search__box{
        width: 80%; 
        margin:0 auto;
        form{
            input{
                height: 50px;
            }
            input[type="text"]{
                width: 80%;
                border-radius: 10px;
                border:3px solid $main-color;
                text-align: center;
            }
            .search__button{
                border:none;
                background-color: white;
                font-size:30px;
                width: 10%;
                color: $button-color;
                border:none;
                position: relative;
                top:8px; left:6px;
            }
        }
    }
}

.section__map{
    img{
        width: 100%; height: 100%;
        margin-top:20px;
    }
}