@import "./variables.module.scss";

//fec 설명 


main{

    // introduce
    .section__introduce{
        .section__introduce__box{
            width: 80%; 
            margin:0 auto;
            margin-top:50px;
            .section__introduce__title{
                font-size: 40px; 
                font-weight: bold;
                margin-bottom:30px;
                line-height: 46px;;
            }
            .section__introduce__text{
                line-height:20px;
                margin-bottom:20px;
                font-weight: 600;
            }
        }
    }

    // Contact
    .section__contact{
        margin-top:70px;
        .section__contact__title{
            width: 80%;
            font-size: 30px;
            font-weight: bold;
            margin:0 auto;
            margin-bottom:30px;
        }
        .section__contact__img{
            width: 100%; height: 320px;
        }
        .section__contact__box{
            width: 80%;
            margin:0 auto;
            margin-top:20px;
            h2{
                font-size:30px;
                margin-bottom:10px;
            }
            P{
                height: 70px;
                
            }
        }
    }
}