@import "./variables.module.scss";
.header{
    background-color: white;
    nav{
        a{
            text-decoration: none;
        }
        
        li:nth-child(2){
            width: 20%;
        }li:nth-child(3){
           width:54%;
        }
        .header__ul{
            display:flex;
            height: 30px;
            font-weight: bold;
            .header__menu{
                text-align: center;
                font-size:18px;
                line-height: 25px;
                img{
                    width: 100%;
                    height: 28px;
                }
            }
          
            .menu__click{
                width: 10%;   
                line-height: 25px;
                cursor: pointer;
                .menu__bar{
                    font-size:32px;
                    margin-left:5px;
                }
                
                .menu__btn{
                    margin-top:12px;
                    position: relative;
                    z-index:1;
                    background-color: white;
                    width: 100vw;
                    height: 750px;
                    
                    transition:all .2s ease-in-out;
                    .logo{
                        img{
                            width: 100px;
                            height: 30px;
                            margin-top:20px;
                        }
                    }
                    .menu{
                        margin-bottom:30px;
                        font-weight: bold;
                        font-size:19px;
                       
                    }
                    
                    > li:nth-child(2){
                        margin-top:30px;
                    }
                    li{
                        width: 80%;
                        border:none;
                        margin-left:17px;
                        ul{
                            .myPageList{
                                margin-left:15px;
                                .myPageList__li{
                                    color:black;
                                    opacity: 0.4;
                                }
                            }
                        }
                    }
                    .loginbox{
                        width: 30%;
                        position: absolute;
                        bottom: 0;
                    }
                    .accountbox{
                        width: 30%;
                        position: absolute;
                        bottom: 0;
                        left:60px;
                    }
                }
                
            }
            
        }
        }
        
    }
