
//Payment information
.payment__information{
    background-color: $background-color;
    width: 90%; height: 500px;
    margin: 0 auto; margin-top:100;
    border-radius: 10px;
    .payment__information__box{
        width: 90%; height: 500px;
        margin:0 auto; margin-top:100px;

        .payment__information__form{
            padding-top:50px;
            text-align: center;
            label{
                margin-right:30px;
            }
        }
        .payment__text__box{
            margin-top:150px;
            font-size:15px;
            p{
                margin-bottom:15px;
            }
        }
    }
    button{
        width: 100%; height: 50px;
        background-color: white;
        border:none;
        position:absolute;
        bottom:0;left:0;
        color:white;
        background-color: $button-color;
    }
}