@import "./variables.module.scss";



// 차 정보(차량번호, 예약시간, 충전시간 등등..)
.carDetailSection{
    width: 90%; height: 500px;
    margin:0 auto;
    margin-top:40px;
    border-radius: 10px;
    background-color: $background-color;
    .carDetailSection__box{
        .carDetailSection__img{
            width: 90%;height: 260px;
            margin:0 auto;
            margin-top:40px;
        }
        .carDetailSection__information{
            margin-top:12px;
            margin-left:19px;
            p{
                margin-bottom:8px;
                opacity: 0.6;
            }
        }
    }
}

//충전 예약취소, 종료버튼
.carDetailCancel{
    width: 100%;
    position: fixed;
    bottom:0;
    
    .carDetailCancel__box{
        
        button{
            height: 50px;
            width: 50%;
            background-color: $button-color;
            color:white;
            border:none;
            font-size:18px;
        }    
    }
}