.loginheader{
    height: 30px;
    display:flex;
    justify-content: space-between;
    a{
        text-decoration: none;
    }
    button{
        background-color: white;
        border:none;
        svg{
            font-size:34px;
        }
    }
    .loginheader__home{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 14%;
        margin-top:6px;
        font-size:29px;
        svg{
            color:black;
        }
    }
    .loginheader__login{
        margin-top:6px;
        text-align: center;
        width: 80%;
        font-size:22px;
    } 
}
