@import "./variables.module.scss";


//FaqNotice
.faqsection{
    .faqsection__box{
        width: 90%;
        margin:0 auto;
        margin-top:46px;
        div{
            height: 43px;
            margin-top:10px;
            background-color: $background-color;
            font-size:15px;
            border-radius:10px;
            border:1px solid $main-color;
            p{
                margin-top:7px;
                margin-left:20px;
            }
            span{
                margin-left:20px;
            }
        }
    }
}