@import "./variables.module.scss";


//main
main{
    height: 100%;
    flex:1;
    a{
        color:black;
        text-decoration: none;
    }
    section{
        margin-top:30px;
        
        .mycar1{
            width: 90%; height: 300px;
            background-color: #f6f6f6;
            border-radius: 10px;
            margin:0 auto;
           
            .mycar__img1{
                width: 100%;
                height: 130px;
                margin-bottom:10px;
            }
            p{
                margin-bottom:5px;
                opacity: 0.5;
            }
            .mycar__text1{
                display:flex;
                flex-direction: column;
                align-items: center;
            }
            .abc1{
                margin-bottom:8px;
            }
            .a1b{
                display:flex;
                justify-content: center;
                div{
                    width: 70px; height: 25px;
                    text-align: center;
                    margin-right:19px;
                    .tag{
                        color:white;
                        font-size:12px;
                    }
                }
            }
        }
        .mycar{
            width: 90%; height: 240px;
            background-color: #f6f6f6;
            border-radius: 10px;
            margin:0 auto;
            margin-top:20px;
            display:flex;
            .mycar__img{
                width: 118px;
                height: 80%;
                margin:20px;
            }
            .mycar__text{
                width: 205px;
                margin:0 auto;
                display:flex;
                flex-direction: column;
                justify-content:center;
                .ab{
                    display:flex;
                }
                p{
                    margin-bottom:10px;
                    opacity: 0.5;
                }
                .abc{
                    color:white;
                    border-radius: 10px;
                    width: 90%; height: 10%;
                    background-color: $button-color;
                    padding-top:5px;
                    margin: 0 auto;
                    position:relative;
                    top:10px;
                    text-align: center;
                }
                .mycar__choice{
                    width: 100px; height: 30px;
                    margin-right:7px;
                    border-radius: 10px;
                    background-color: $main-color;
                    display:flex;
                    flex-direction: column;
                    justify-content: center;
                    text-align: center;
                    a{
                        color:white;
                    }
                }
                .mycar__correction{
                    border-radius: 10px;
                        width: 100px;
                        height: 30px;
                        display:flex;
                        justify-content: center;
                        align-items: center;
                        background-color: $main-color;
                        a{
                            color:white;
                        }
                    }
            }
            
        }
        
    }
}
// 원본
.mycar__correction1{
    background-color: $main-color;
    border-radius: 10px;
}
.mycar__choice1{
    background-color: $main-color;
    border-radius: 10px;
}