main{
    
    .userinformation__section{
        .inner_header{
            height: 600px;
            width: 100%;
            background-image: url(../img/전기차.jpg);
            background-size:cover;
            
            .info_text{
                p{
                    position:relative;
                    top:150px;
                }            
                color:white;
            }
            p{
                text-align: center;
            }
            p:nth-child(1) {
                font-size:53px;
                text-align: center;
            }
            p:nth-child(2) {
                margin-top:30px;
                line-height: 20px;
            }
        }
         .info_text:before{
            content:'';
            background-color: rgba(0,0,0,0.54);
            position:absolute;
            height: 600px;
            top: 60px; bottom:0; left:0; right:0;

        }


        .information_account{
            height: 190px;
            text-align: center;
            h2{
                padding-top:40px;
                font-size:22px;
                line-height: 30px;;
            }
            .information_box{
                border:1px solid rgba(0,0,0,0.5);
                border-radius: 50px;
                width: 200px; height: 50px;
                margin:0 auto;
                margin-top:20px;
                .information_href{
                    line-height: 46px;;
                }
            }
        }

        //charge
        .charge{
            margin-top:20px;
            height: 400px;
            .charge_way{
                width: 200px;
                margin:20px;
                font-size:26px;
                font-weight: bold;
            }
            p{
                margin-left:20px;
                line-height: 23px;
                opacity: 0.8;
            }
            .charge_img{
             
                background-size: cover;
                width: 100%;height: 220px;
            }
    
        } 

       
    }


